e","sourcesContent":["\n@charset \"UTF-8\";\n/*\r\n * mescroll -- 精致的下拉刷新和上拉加载js框架  ( a great JS framework for pull-refresh and pull-up-loading )\r\n * version 1.1.6\r\n * 2017-08-27\r\n * https://github.com/mescroll/mescroll.git\r\n * http://www.mescroll.com\r\n * author: wenju < mescroll@qq.com > 文举\r\n * \r\n * ----- mescroll的html结构解析 ----\r\n * \r\n   <div id=\"mescroll\" class=\"mescroll\">\r\n\t\r\n\t\t//下拉刷新区域 ( mescroll初始化之后自动创建 )\r\n\t\t<div class=\"mescroll-downwarp\">\r\n\t\t\t<div class=\"downwarp-content\">\r\n\t\t\t\t<p class=\"downwarp-progress\"></p> <p class=\"downwarp-tip\">下拉刷新 </p>\r\n\t\t\t</div>\r\n\t\t</div>\r\n\t\t\r\n\t\t//界面的具体内容\r\n\t\t//<div>界面内容</div>\r\n\t\t//数据列表..\r\n\t\t//<ul id=\"dataList\" class=\"data-list\">\r\n\t\t//\t<li>数据列表</li>\r\n\t\t\r\n\t\t\t//空布局 ( 列表无任何数据时, 自动创建显示 )\r\n\t\t\t<div class=\"mescroll-empty\">\r\n\t\t\t\t<img class=\"empty-icon\" src=\"../img/mescroll-empty.png\"/>\r\n\t\t\t\t<p class=\"empty-tip\">暂无相关数据~</p>\r\n\t\t\t\t <p class=\"empty-btn\">去逛逛 ></p> \r\n\t\t\t</div>\r\n\t\t\t\r\n\t\t//</ul>\r\n\t\r\n\t\t//上拉加载区域 ( mescroll初始化之后自动创建 )\r\n\t\t<div class=\"mescroll-upwarp\">\r\n\t\t\t//加载中..\r\n\t\t\t<p class=\"upwarp-progress mescroll-rotate\"></p><p class=\"upwarp-tip\">加载中..</p>\r\n\t\t\t//无数据\r\n\t\t\t<p class=\"upwarp-nodata\">-- END --</p>\r\n\t\t</div>\r\n\t\t\r\n\t\t//回到顶部按钮 ( 列表滚动到配置的距离时, 自动创建显示 )\r\n\t\t<img class=\"mescroll-totop\" src=\"../img/mescroll-totop.png\"/>\r\n\t\t\r\n\t</div>\r\n * \r\n */\n/*下拉刷新和上拉加载的滑动区域*/\n.mescroll {\n  width: 100%;\n  height: 100%;\n  overflow-y: auto;\n  -webkit-overflow-scrolling: touch;\n}\n\n/*启用硬件加速:动画渲染流畅,解决IOS下拉刷新因隐藏进度条致使的闪屏问题,在下拉刷新和上拉加载触发时启用,结束后移除,避免滥用导致其他兼容性问题*/\n.mescroll-hardware {\n  -webkit-transform: translateZ(0);\n  transform: translateZ(0);\n}\n\n/*下拉刷新区域*/\n.mescroll-downwarp {\n  position: relative;\n  width: 100%;\n  height: 0;\n  overflow: hidden;\n  text-align: center;\n}\n\n/*下拉刷新--高度重置的过渡动画*/\n.mescroll-downwarp-reset {\n  -webkit-transition: height 300ms;\n  transition: height 300ms;\n}\n\n/*下拉刷新--内容区,定位于区域底部*/\n.mescroll-downwarp .downwarp-content {\n  position: absolute;\n  left: 0;\n  bottom: 0;\n  width: 100%;\n  min-height: 30px;\n  padding: 10px 0;\n}\n\n/*上拉加载区域*/\n.mescroll-upwarp {\n  min-height: 30px;\n  padding: 15px 0;\n  text-align: center;\n  visibility: hidden;\n  /*代替display: none,列表快速滑动到底部能及时显示上拉加载的区域*/\n}\n\n/*下拉刷新,上拉加载--提示文本*/\n.mescroll-downwarp .downwarp-tip,\n.mescroll-upwarp .upwarp-tip,\n.mescroll-upwarp .upwarp-nodata {\n  display: inline-block;\n  font-size: 12px;\n  color: gray;\n  vertical-align: middle;\n}\n\n/*下拉刷新,上拉加载--旋转进度条*/\n.mescroll-downwarp .downwarp-progress,\n.mescroll-upwarp .upwarp-progress {\n  display: inline-block;\n  width: 16px;\n  height: 16px;\n  border-radius: 50%;\n  border: 1px solid gray;\n  margin-right: 8px;\n  border-bottom-color: transparent;\n  vertical-align: middle;\n}\n\n/*旋转动画*/\n.mescroll-rotate {\n  -webkit-animation: mescrollRotate 0.6s linear infinite;\n  animation: mescrollRotate 0.6s linear infinite;\n}\n@-webkit-keyframes mescrollRotate {\n0% {\n    -webkit-transform: rotate(0deg);\n}\n100% {\n    -webkit-transform: rotate(360deg);\n}\n}\n@keyframes mescrollRotate {\n0% {\n    transform: rotate(0deg);\n}\n100% {\n    transform: rotate(360deg);\n}\n}\n\n/*无任何数据的空布局*/\n.mescroll-empty {\n  width: 100%;\n  padding-top: 20px;\n  text-align: center;\n}\n.mescroll-empty .empty-icon {\n  width: 90px;\n  height: 90px;\n}\n.mescroll-empty .empty-tip {\n  margin-top: 6px;\n  font-size: 14px;\n  color: gray;\n}\n.mescroll-empty .empty-btn {\n  max-width: 50%;\n  margin: 20px auto;\n  padding: 10px;\n  border: 1px solid #65AADD;\n  border-radius: 6px;\n  background-color: white;\n  color: #65AADD;\n}\n.mescroll-empty .empty-btn:active {\n  opacity: .75;\n}\n\n/*回到顶部的按钮*/\n.mescroll-totop {\n  z-index: 9990;\n  position: fixed;\n  right: 10px;\n  bottom: 30px;\n  width: 36px;\n  height: 36px;\n  border-radius: 50%;\n  opacity: 0;\n}\n\n/*显示动画--淡入*/\n.mescroll-fade-in {\n  -webkit-animation: mescrollFadeIn .5s linear forwards;\n  animation: mescrollFadeIn .5s linear forwards;\n}\n@-webkit-keyframes mescrollFadeIn {\n0% {\n    opacity: 0;\n}\n100% {\n    opacity: 1;\n}\n}\n@keyframes mescrollFadeIn {\n0% {\n    opacity: 0;\n}\n100% {\n    opacity: 1;\n}\n}\n\n/*隐藏动画--淡出*/\n.mescroll-fade-out {\n  pointer-events: none;\n  -webkit-animation: mescrollFadeOut .5s linear forwards;\n  animation: mescrollFadeOut .5s linear forwards;\n}\n@-webkit-keyframes mescrollFadeOut {\n0% {\n    opacity: 1;\n}\n100% {\n    opacity: 0;\n}\n}\n@keyframes mescrollFadeOut {\n0% {\n    opacity: 1;\n}\n100% {\n    opacity: 0;\n}\n}\n\n/*滚动条轨道背景(默认在PC端设置)*/\n.mescroll-bar::-webkit-scrollbar-track {\n  background-color: transparent;\n}\n\n/*滚动条轨道宽度 (默认在PC端设置)*/\n.mescroll-bar::-webkit-scrollbar {\n  width: 6px;\n}\n\n/*滚动条游标 (默认在PC端设置)*/\n.mescroll-bar::-webkit-scrollbar-thumb {\n  border-radius: 6px;\n  background-color: #ccc;\n}\n\n/*滚动条游标鼠标经过的颜色变化 (默认在PC端设置)*/\n.mescroll-bar::-webkit-scrollbar-thumb:hover {\n  background-color: #aaa;\n}\n"],"sourceRoot":""}]);

